body {
  font-family: sans-serif;
  background-color: rgba(0, 0, 0, 0.05);
}

table {
  border: 1px solid;
  border-collapse: collapse;
  overflow: scroll;
}

td,
th {
  padding: 8px 12px;
  text-align: center;
  border: 1px solid rgb(0, 0, 0, 0.6);
  white-space: nowrap;
}

thead tr {
  background: rgb(0, 0, 0, 0.2);
}

tbody tr:hover {
  background: rgb(0, 0, 0, 0.1);
}

.container {
  width: 100%;
  max-width: 100%;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: auto;
}

.column {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  justify-content: center;
}

text.large {
  font-size: 1.5rem;
}

text.medium {
  font-size: 1.25rem;
}

text.small {
  font-size: 0.75rem;
}

.centered {
  margin-left: auto;
  margin-right: auto;
}

text.title {
  font-size: 2rem;
}

.table-container {
  max-width: 100%;
  overflow: auto;
}

.header {
  height: 150px;
  align-items: center;
  text-align: center;
}

.footer {
  height: 150px;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.icon img {
  height: 16px;
  width: 16px;
}

a {
  cursor: pointer;
  text-decoration: none;
}

img {
  color: grey;
}

.heart-icon{
    margin-left: 4px;
    margin-right: 4px;
}